import React from "react";
import TopBanner from '../../components/TopBanner'
import Preview from '../../components/Preview'
import Helmet from "react-helmet";

export default function OwnWeight({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div className="preview-feed">
      <Helmet title={`Инвентарь - Вес`} />
      <TopBanner style={OwnWeightStyle} title={'Упражнения с собственными весом'} />
      {posts
        .filter(post => post.node.frontmatter.filterinventory === 'ownweight')
        .map(({ node: post }) => {
          return (
            <Preview post={post} />
          );
        })}
    </div>
  );
}

const OwnWeightStyle = {
	backgroundImage: 'url(http://bestbodyblog.com/wp-content/uploads/2018/01/9.jpg)',
	backgroundPosition: "center"
};

export const pageQuery = graphql`
  query OwnWeightQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 180)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            image
            image_2
            image_3
            image_4
            filterby
            efficiency
            injury
            bodypart
            filtertype
            type
            filterinventory
            inventory
            filterplace
          }
        }
      }
    }
  }
`;



